*, *::before, *::after {
  box-sizing: inherit;
  outline: 0.1rem dashed lightgrey; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #2c2929;
  color: #ffffff; }

body {
  overflow-x: hidden;
  max-width: 100%;
  font-size: 1.4rem;
  -webkit-font-smoothing: antialiased;
  font-variant-numeric: proportional-nums;
  font-feature-settings: "pnum"; }

img {
  width: 100%;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  color: #ed4040; }
  a:hover, a:active {
    text-decoration: underline; }

ul {
  padding: 0;
  list-style-type: none; }



